}
.main-nav{
    background-color: #191919;
    color: #AFAFAF;
    height: 16vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding-left: 100px;
    padding-right: 100px;
}
.logo{
    font-size: 20px;
    letter-spacing: 2px;
    cursor: pointer;
}
.logo:hover{
    color: ivory;
    transition-duration: 250ms;
}
.links{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 50%;
    list-style-type: none;  
}
.links li{
    cursor: pointer;
    transition-duration: 250ms;
}
.links li:hover{
    color: ivory;
}
.burger {
    display: none;
}
.slideNav{
    background-color: #191919;
    color: #AFAFAF;
    border-top: 1px solid #AFAFAF;
    height: 84vh;
    position: fixed;
    display: none;
    z-index: 10;
    transition-duration: 250ms;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: hidden;
}
.slideNav ul{
    list-style-type: none;
    font-size: 35px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    height: 60%;
}
.router-links{
    text-decoration: none;
    color:#AFAFAF;
}
.router-links:hover{
    color: ivory;
    transition-duration: 250ms;
}
.github-nav{
    transition-duration: 250ms;
    font-size: 50px;
    cursor: pointer;
}
@media only screen and (max-width: 800px) {
    .main-nav{
        padding-left: 25px;
        padding-right: 25px;
    }
    .burger {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        height: 35px;
        width: 50px;
        cursor: pointer;
    }
    .burger div{
        background-color: #AFAFAF;
        border-radius: 25px;
        height: 4.5px;
        width: 100%; 
        position: relative;
        transition-duration: 250ms;
    }
    .links{
        display: none;
    }
    .slideNav{
        display: -webkit-flex;
        display: flex;
    }
  }
