@-webkit-keyframes load {
    0%   {width: 100%; display: -webkit-flex; display: flex; font-size: 70px }
    33.33332%   {width: 100%; display: -webkit-flex; display: flex; font-size: 70px }
    33.33333%  {width:0%; display: none; font-size: 0px} 
    100%  {width:0%; display: none; font-size: 0px}
  }
@keyframes load {
    0%   {width: 100%; display: -webkit-flex; display: flex; font-size: 70px }
    33.33332%   {width: 100%; display: -webkit-flex; display: flex; font-size: 70px }
    33.33333%  {width:0%; display: none; font-size: 0px} 
    100%  {width:0%; display: none; font-size: 0px}
  }
  @-webkit-keyframes loaddots {
    0%{background-color: #AFAFAF;}
    30%{background-color: #AFAFAF;}
    33.333333%{background-color: #191919;}
    100% {background-color: #191919;}
  }
  @keyframes loaddots {
    0%{background-color: #AFAFAF;}
    30%{background-color: #AFAFAF;}
    33.333333%{background-color: #191919;}
    100% {background-color: #191919;}
  }
